@import '../../styles/colors';
@import '../../styles/mixins';
@import '../../styles/variables';

.home {
  flex-grow: 1;
  display: flex;

  &__hero {
    position: relative;
    flex-grow: 1;
    overflow: hidden;
    max-height: 100vh;
    max-width: 100vw;
  }

  &__mark {
    cursor: pointer;
    width: 120px;
    height: 120px;
    position: absolute;
    z-index: 3;
    bottom: 100px;
    right: 100px;

    @include media(sm) {
      left: 50%;
      bottom: 23px;
      transform: translateX(-50%);
    };
    
    &-circle {
      width: 100%;
      height: 100%;
      animation: rotate 12s linear infinite;
    }

    &-arrow {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transform-origin: center;

      @include media(sm) {
        transform: translate(-50%, -50%) rotate(90deg);
      }
    }
  }

  &__button {
    cursor: pointer;
    font-weight: 700;
    @include toRem(18);
    line-height: 1.2;
    @include flex;
    color: $color_white;
    padding: toEm(10);
    @include no-border;
    background-color: transparent;
    border-bottom: 1px solid transparent;
    transition: transition(transform), transition(border-color);

    &:hover {
      border-color: $color_white;
      transform: translateY(1px);
    }
  }

  &__content {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    @include flex;
    flex-direction: column;
    text-align: center;
  }

  & .title {
    max-width: 566px;

    @include media(sm) {
      @include toRem(55);
    };

    @include media(tn) {
      @include toRem(32);
    };
  }

  &__subtitle {
    font-weight: 400;
    @include toRem(16);
    line-height: 1.4;
    color: $color_white;
  }

  &__logo {
    position: absolute;
    left: 60px;
    top: 40px;
    width: 83px;
    height: 40px;
    z-index: 2;

    @include media(sm) {
      left: 50%;
      transform: translateX(-50%)
    };
  }

  &__video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__content-mask {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background-color: $dark-bg;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  } 100% {
    transform: rotate(360deg);
  }
}